
body{
    background-image: url("https://images.unsplash.com/photo-1580193769210-b8d1c049a7d9?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=874&q=80");
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    
}
input{
   
    border-radius: 20px;
    border:none;
    background-color: white;
    padding: 10px;
   color: black;
   
}
button{
    width: 100px;
    padding: 10px;
    border-radius: 20px;
    
    background-color: transparent;
    border: none;
    color: white;
    
}
.card{
    padding: 0px;
    margin: 0px;
    background-image: url("https://images.unsplash.com/photo-1580193769210-b8d1c049a7d9?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=874&q=80");
    background-repeat: no-repeat;
    background-size: cover;
    border: none;
}
.card_down{
    height: 100px;
    width: 100px;
    display: flex;
    justify-content: center;
    border: 1px solid;
    border-radius: 20px;
    margin: auto;
    align-items: center;
}
.card_down:hover{
    box-shadow: 1px 2px 1px 2px gray;
}

@media only screen and (max-width: 600px) {
    .row>*{
        width: 50%;
        
    }
    .row{
        margin-top: 50px;
    }
    body{
        background-image: url("https://wallpaperaccess.com/full/5667858.jpg");
        background-repeat: no-repeat;
        background-size:100%
        
    }
   
    .card{
    
        background-image: url("https://wallpaperaccess.com/full/5667858.jpg");
        background-repeat: no-repeat;
        background-size: cover;
    }
    
  
  }